import { AbsoluteFill, useCurrentFrame, Audio } from "remotion";
import { Video } from "@remotion/media";
import { loadFont } from "@remotion/google-fonts/NotoSansDevanagari";

loadFont();

type Caption = {
  start: number;
  end: number;
  text: string;
};

export const CaptionedVideo = ({
  videoPath,
  captions,
  style,
}: {
  videoPath?: string;
  captions: Caption[];
  style: "top" | "bottom" | "karaoke";
}) => {
  const frame = useCurrentFrame();
  const time = frame / 30;

  if (!videoPath) {
    return null;
  }

  const currentCaption = captions.find(
    (c) => time >= c.start && time <= c.end
  );

  // Karaoke style - highlight word by word
  const renderKaraokeText = (text: string) => {
    const words = text.split(" ");
    const totalDuration = currentCaption ? currentCaption.end - currentCaption.start : 1;
    const wordDuration = totalDuration / words.length;
    const currentWordIndex = Math.min(
      Math.floor((time - (currentCaption?.start || 0)) / wordDuration),
      words.length - 1
    );

    return words.map((word, index) => (
      <span
        key={index}
        style={{
          color: index <= currentWordIndex ? "#ff6b6b" : "white",
          transition: "color 0.2s ease",
          marginRight: "8px",
        }}
      >
        {word}
      </span>
    ));
  };

  return (
    <AbsoluteFill style={{ backgroundColor: "black" }}>
      {/* Video with audio track */}
      <Video 
        src={videoPath} 
        volume={1}
        style={{ width: "100%", height: "100%" }}
      />
      
      {/* Audio track (redundant but ensures audio is included) */}
      <Audio src={videoPath} volume={1} />
      
      {/* Caption overlay */}
      {currentCaption && (
        <div
          style={{
            position: "absolute",
            bottom: style === "bottom" ? 80 : undefined,
            top: style === "top" ? 40 : undefined,
            left: 0,
            right: 0,
            width: "100%",
            textAlign: "center",
            fontFamily: "Noto Sans Devanagari, sans-serif",
            fontSize: style === "karaoke" ? 36 : 42,
            color: "white",
            textShadow: "2px 2px 6px black",
            backgroundColor: style === "karaoke" ? "rgba(0, 0, 0, 0.7)" : "transparent",
            padding: style === "karaoke" ? "15px 25px" : "10px",
            borderRadius: style === "karaoke" ? "12px" : "0",
            margin: "0 auto",
            maxWidth: style === "karaoke" ? "80%" : "90%",
            lineHeight: 1.4,
          }}
        >
          {style === "karaoke" 
            ? renderKaraokeText(currentCaption.text)
            : currentCaption.text
          }
        </div>
      )}
    </AbsoluteFill>
  );
};